@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  src: url("../../source/fonts/montserrat-regular.woff2") format("woff2"), url("../../fonts/montserrat-regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: bold;
  font-weight: 700;
  src: url("../../fonts/montserrat-bold.woff2") format("woff2"), url("../../fonts/montserrat-bold.woff") format("woff");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
}

.container {
  flex-grow: 1;
  width: 280px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .container {
    width: 728px;
  }
}
@media (min-width: 1440px) {
  .container {
    width: 1360px;
  }
}

.page {
  height: 100%;
}

.page__body {
  max-width: 100%;
  min-height: 100%;
  margin: 0;
  display: flex;
  flex-direction: column;
  background-color: #ffffff;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 14px;
  line-height: 20px;
  color: #333333;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.button {
  min-width: 113px;
  display: block;
  padding: 8px 20px;
  border: 0;
  border-radius: 4px;
  font-family: "Montserrat", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  text-align: center;
  color: #ffffff;
  background-color: #7859cf;
  text-decoration: none;
  cursor: pointer;
}
.button:hover,
.button :focus {
  background-color: #9070ec;
}
.button:active {
  background-color: #593cac;
}
.button:disabled {
  background-color: #bdbdbd;
}

