}

.main-navigation__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.main-navigation__list a {
  color: #333333;
}

.main-navigation__toggle {
  position: relative;
  width: 60px;
  height: 60px;
  display: block;
  cursor: pointer;
  padding: 0;
  border: none;
  background-color: transparent;
  box-sizing: border-box;
}
.main-navigation__toggle:active {
  background-color: #f0ebff;
}
@media (min-width: 768px) {
  .main-navigation__toggle {
    display: none;
  }
}

@media (min-width: 768px) {
  .main-navigation__wrapper {
    order: -1;
  }
}

.main-navigation--closed .main-navigation__toggle::before {
  position: absolute;
  top: 24px;
  left: 24px;
  content: "";
  width: 16px;
  height: 2px;
  background-color: #333333;
  box-shadow: 0 4px 0 0 #333333, 0 8px 0 0 #333333;
}

@media (max-width: 767px) {
  .main-navigation--closed .main-navigation__wrapper {
    display: none;
  }
}
@media (max-width: 767px) {
  .main-navigation--closed .main-navigation__wrapper {
    display: none;
  }
}
.main-navigation--opened .main-navigation__toggle::before {
  position: absolute;
  content: "";
  top: 28px;
  right: 18px;
  width: 16px;
  height: 2px;
  transform: rotate(45deg);
  box-shadow: none;
  background-color: #333333;
}
.main-navigation--opened .main-navigation__toggle::after {
  position: absolute;
  content: "";
  top: 28px;
  left: 25px;
  width: 16px;
  height: 2px;
  transform: rotate(-45deg);
  box-shadow: none;
  background-color: #333333;
}

@media (max-width: 767px) {
  .main-navigation--opened .main-navigation__wrapper {
    position: absolute;
    z-index: 10;
    top: 60px;
    left: 0;
    width: 100%;
    background-color: #ffffff;
  }
}

.main-navigation--nojs .main-navigation__toggle {
  display: none;
}

.main-navigation--nojs .site-list__item {
  display: block;
}

.main-navigation--nojs .main-navigation__wrapper {
  position: static;
}

